<template>
<div class="ickt-search">
    <input type="text" :placeholder="placeholder" v-model="msg" @keydown.enter="showSearch">
</div>
</template>
<style scoped lang="scss">
.ickt-search {
    background-color: #fff;
    padding: 10px 20px;
    display: flex;
    input {
        flex: 1;
        border: none;
        outline: none;
        background: #efefef;
        font-size: 12px;
        padding: 10px 20px;
        border-radius: 17px;
    }
}
</style>
<script>
export default {
    // 接收属性
    props: ['placeholder', 'search'],
    // 数据
    data() {
        return {
            msg: ''
        }
    },
    // 方法
    methods: {
        // 显示搜索结果
        showSearch() {
            // 将数据提交给父组件
            this.search(this.msg)
            // 清空内容
            this.msg = '';
        }
    }
}
</script>