<template>
<div class="page-list">
    <Search placeholder="请输入搜索内容" :search="e => $store.commit('search', e)"></Search>
    <!-- <h1>{{$store.state.search}}</h1> -->
    <!-- 排序模块 -->
    <ul class="orders">
        <li v-for="item in orders" :key="item.id" @touchend="orderList(item.id)">{{item.text}}</li>
    </ul>
    <Product v-for="item in list" :key="item._id" :data="item"></Product>
    <!-- <Product v-for="item in dealList" :key="item._id" :data="item"></Product> -->
    <!-- <div class="loader-more" @touchend="showMore" v-show="others.length"> -->
    <div class="loader-more" @touchstart="showMore" v-show="others.length">
        <span>查看其它{{others.length}}条商品</span>
    </div>
</div>
</template>
<style scoped lang="scss">
@import '@/base.scss';
.page-list {
    .orders {
        background-color: #fff;
        border-bottom: 1px solid #ccc;
        height: 30px;
        line-height: 30px;
        display: flex;
        text-align: center;
        font-size: 14px;
        li {
            flex: 1;
            width: (100%/4);
            border-right: 1px solid #ccc;
            &:last-child {
                border-right: none;
            }
            &::after {
                @include arrow(4px);
                content: "";
                position: relative;
                top: -2px;
                right: -1px;
            }
        }
    }
    .loader-more {
        background-color: #fff;
        border-bottom: 1px solid #ccc;
        height: 40px;
        line-height: 40px;
        text-align: center;
        color: $color;
        text-indent: -1em;
        span {
            position: relative;
            &::before,
            &::after {
                @include arrow(7px, $color);
                content: "";
                position: absolute;
                top: 10px;
                right: -18px;
            }
            &::after {
                top: 8px;
                border-top-color: #fff;
            }
        }
    }
}
</style>
<script>
// 引入组件
import Search from '@/components/Search';
// 商品组件
import Product from '@/components/Product';
export default {
    // 注册组件
    components: { Search, Product },
    // 数据
    data() {
        return {
            // 排序数据
            orders: [
                { text: '价格排序', id: 'price' },
                { text: '销量排序', id: 'sales' },
                { text: '好评排序', id: 'evaluate' },
                { text: '优惠排序', id: 'discount' }
            ],
            // 商品列表
            list: [],
            // 剩余的成员
            others: []
        }
    },
    // 计算属性数据
    computed: {
        // 对列表数据做过滤
        // dealList() {
        //     return this.list.filter(item => item.title.toUpperCase().indexOf(
        //             // 包含store中的搜索词
        //             this.$store.state.search.toUpperCase()
        //         ) >= 0
        //     )
        // },
        // 代理state数据
        dealSearch() {
            return this.$store.state.search;
        }
    },
    // 定义方法
    methods: {
        // 请求数据
        getData() {
            // console.log(this.$route)
            // 解构动态路由参数
            let{ params } = this.$route;
            // 获取数据
            this.$http
                // .get('/data/list?id=' + params.id)
                // .get('/data/list', { params: { id: params.id } })
                // .get('/data/list', { params: params })
                .get('/static/data/list.json', { params })
                // 监听数据返回
                .then(({ data }) => {
                    // 存储数据
                    this.list = data.slice(0, 3);
                    // 存储剩余成员
                    this.others = data.slice(3);
                })
        },
        // 显示剩余商品
        showMore() {
            // 300毫秒之后更新：解决点击穿透问题
            setTimeout(() => {
                // 显示剩余的商品就是将others中的数据，放在list中
                this.list = this.list.concat(this.others)
                // 清空others
                this.others = [];
            }, 300)
            // console.log(1111, this)
            // 显示剩余的商品就是将others中的数据，放在list中
            // this.list = this.list.concat(this.others)
            // // 清空others
            // this.others = [];
        },
        // 商品排序
        orderList(id) {
            // console.log(id)
            // 对list数据做排序
            // 如果优惠排序
            // if (id === 'discount') {
            //     this.list.sort((a, b) => {
            //         // 优惠就是：原价 - 现价
            //         // a的优惠： a.originPrice - a.price
            //         // b的优惠： b.originPrice - b.price
            //         // 升序：a的优惠 - b的优惠
            //         // return (a.originPrice - a.price) - (b.originPrice - b.price)
            //         // 降序
            //         return (b.originPrice - b.price) - (a.originPrice - a.price)
            //     })
            // } else {
            //     // 普通排序
            //     this.list.sort((a, b) => {
            //         // 升序
            //         // return a[id] - b[id]
            //         // 降序
            //         return b[id] - a[id]
            //     })
            // }
            // 发送请求
            this.$http
                .get('/static/data/order.json', {
                    params: {
                        // 分类
                        type: this.$route.params.id,
                        // 排序字段
                        id,
                        // 升序还是降序, 1 升序，-1是降序
                        mode: -1
                    }
                })
                // 监听数据返回
                .then(({ data }) => {
                    // 如果有数据返回
                    // console.log(data)
                    // 没有数据，提示用户
                    if (!data.length) {
                        alert('没有数据')
                    }
                    // 更新视图就是更新数据
                    this.list = data.slice(0, 3);
                    this.others = data.slice(3);
                })
        }
    },
    // 组件创建完成
    created() {
        // 发送请求
        this.getData()
    },
    // 监听器
    watch: {
        // 路由变化
        $route() {
            // 发送请求
            this.getData()
        },
        // 监听状态数据
        // '$store.state.search'(value) {
        //     console.log(value)
        // }
        // 监听被代理的state数据
        dealSearch(value) {
            // console.log(value)
            // 发送请求
            this.$http
                .get('/static/data/search.json', {
                    // query
                    params: {
                        // 分类
                        type: this.$route.params.id,
                        // 搜索的内容
                        value
                    }
                })
                // 监听数据返回
                .then(({ data }) => {
                    // 没有数据，提示用户
                    if (!data.length) {
                        alert('没有数据')
                        // 清空内容
                        this.list = [];
                        this.others = [];
                        return;
                    }
                    // 存储数据
                    this.list = data.slice(0, 3);
                    this.others = data.slice(3);
                })
        }
    }
}
</script>